import styled from 'styled-components'

import { maxWidth } from './../../utils/constants'

const ContentWrapper = styled.div`
  max-width: ${maxWidth};
  width: 100%;
  margin-left: 15px;
  margin-right: 15px;
  height: 100%;
`

export default ContentWrapper



